body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  display: flex;
}

.card__img {
  width: 160px;
  margin: 16px 3px 0;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
}

.card__raiting {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 16px;
  font-size: 10px;
  font-weight: 400;
  display: flex;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__price_value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

